import ApiService from "@/core/services/ApiService";
import { Action, Module, Mutation, VuexModule } from "vuex-module-decorators";
import { computed, watch } from "vue";

interface iPols {
  pol_countryname: "";
  polname: "";
  tmh_pol: "";
}

interface iPods {
  podcountryname: "";
  podname: "";
  tmh_pod: "";
}

@Module
export default class MaritimeFileModule extends VuexModule {
  activeSection = "sheludes";
  registerSection = false;
  loginSection = false;
  fromSection3 = false;

  pols_array: iPols[] = [];
  pods_array: iPods[] = [];

  schedule_pol = "";
  schedule_pod = "";
  schedule_date = "";
  weeks = 2;

  rate_pol = "";
  rate_pod = "";

  container_type = "";

  booking_quotation = undefined;
  see_booking = false;

  discount_date = "";
  find_schedules = false;
  selected_container = "";
  show_auth_modal = false;

  language = 'es';

  get Active(): any {
    return this.activeSection;
  }

  get Register(): any {
    return this.registerSection;
  }

  get Login(): any {
    return this.loginSection;
  }

  get FromSection(): boolean {
    return this.fromSection3;
  }

  get Pols(): iPols[] {
    return this.pols_array;
  }

  get Pods(): iPods[] {
    return this.pods_array;
  }

  get Pol(): string {
    return this.schedule_pol;
  }

  get Pod(): string {
    return this.schedule_pod;
  }

  get PolRate(): string {
    return this.rate_pol;
  }

  get PodRate(): string {
    return this.rate_pod;
  }

  get ScheduleDate(): string {
    return this.schedule_date;
  }

  get ScheduleWeeks(): number {
    return this.weeks;
  }

  get ContainerTypeItem(): string {
    return this.container_type;
  }

  get BookingQuotationt(): any {
    return this.booking_quotation;
  }

  get SeeBooking(): any {
    return this.see_booking;
  }

  get DiscountDate(): any {
    return this.discount_date;
  }

  get FindSchedules(): boolean {
    return this.find_schedules;
  }

  get SelectedContainer(): string {
    return this.selected_container;
  }

  get ShowAuthModal(): boolean {
    return this.show_auth_modal;
  }

  get Language(): string {
    return this.language;
  }

  @Mutation
  ["setActive"](payload) {
    this.activeSection = payload;
  }

  @Mutation
  ["setRegister"](payload) {
    this.registerSection = payload;
  }

  @Mutation
  ["setLogin"](payload) {
    this.loginSection = payload;
  }

  @Mutation
  ["setFromSection3"](payload) {
    this.fromSection3 = payload;
  }

  @Mutation
  ["setPols"](payload) {
    this.pols_array = payload;
  }

  @Mutation
  ["addPols"](payload) {
    if (
      this.pols_array.findIndex((x) => x.polname === payload.polname) === -1
    ) {
      this.pols_array.push(payload);
    }
  }

  @Mutation
  ["setPods"](payload) {
    this.pods_array = payload;
  }

  @Mutation
  ["addPods"](payload) {
    if (
      this.pods_array.findIndex((x) => x.podname === payload.podname) === -1
    ) {
      this.pods_array.push(payload);
    }
  }

  @Mutation
  ["setPol"](payload) {
    this.schedule_pol = payload;
  }

  @Mutation
  ["setPod"](payload) {
    this.schedule_pod = payload;
  }

  @Mutation
  ["setRatePol"](payload) {
    this.rate_pol = payload;
  }

  @Mutation
  ["setRatePod"](payload) {
    this.rate_pod = payload;
  }

  @Mutation
  ["setDate"](payload) {
    this.schedule_date = payload;
  }

  @Mutation
  ["setWeeks"](payload) {
    this.weeks = payload;
  }

  @Mutation
  ["setContainerType"](payload) {
    this.container_type = payload;
  }

  @Mutation
  ["setBookingQuotation"](payload) {
    this.booking_quotation = undefined;
    this.booking_quotation = payload[0];
  }

  @Mutation
  ["resetBookingQuotation"]() {
    this.booking_quotation = undefined;
  }

  @Mutation
  ["setSeeForm"](payload) {
    this.see_booking = payload;
  }

  @Mutation
  ["setDiscountDate"](payload) {
    this.discount_date = payload;
  }

  @Mutation
  ["seFindSchedule"](payload) {
    this.find_schedules = payload;
  }

  @Mutation
  ["seSelectedContainer"](payload) {
    this.selected_container = payload;
  }

  @Mutation
  ["setShowAuthModal"](payload) {
    this.show_auth_modal = payload;
  }

  @Mutation
  ["setLanguage"](payload) {
    this.language = payload;
  }

  @Mutation
  ["resetModule"]() {
    this.schedule_pol = "";
    this.schedule_pod = "";
    this.schedule_date = "";
    this.weeks = 2;
    this.booking_quotation = undefined;
    this.see_booking = false;
    this.discount_date = "";
    this.find_schedules = false;
    this.selected_container = "";
    this.show_auth_modal = false;
    this.language = 'es';
  }
}
